.container-searchbar--red {
  background-color: rgb(252, 231, 231);
  border-radius: 22px;
  padding: 15px 10px;
  padding-left: 70px;
  border-width: 0;
  width: 100%;
  outline: 0;
}

.container-searchbar {
  background-color: white;
  border-radius: 22px;
  padding: 15px 10px;
  padding-left: 70px;
  border-width: 0;
  width: 100%;
  outline: 0;
}

.container-searchbar--red::placeholder {
  color: rgb(246, 129, 132);
  font-size: 1em;
}

.container-search {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.container-wrapper {
  width: 80%;
}
.container-icon {
  position: absolute;
  height: 100%;
  width: 40px;
  display: flex;
  padding-left: 15px;
  justify-content: center;
  align-items: center;
}
